// Directorist Dropdown Style
.directorist-dropdown{
    position: relative;
    .directorist-dropdown__toggle{
        color: $body-color;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 100%;
        position: relative;
        &.directorist-toggle-hasIcon{
            &:after{
                content: '\f347';
                font: normal 12px/1 dashicons;
            }
        }
    }
    .directorist-dropdown__links{
        display: none;
        position: absolute;
        width: 100%;
        height: 120px;
        overflow-y: auto;
        left: 0;
        top: 44px;
        padding: 15px;
        background-color: #fff;
        box-shadow: 0 10px 40px rgba(134,142,174,.18823529411764706);
        border-radius: 5px;
        z-index: 99999;
        a{
            display: block;
            font-size: 14px;
            font-weight: 500;
            text-decoration: none;
            display: block;
            padding: 9px 10px;
            border-radius: 4px;
            color: #5a5f7d;
        }
    }
}